
.page-services {
  padding-bottom: 95px;
  box-sizing: border-box;
  margin: 0;
  width: 100%;
}
.page-services .content {
    margin-left: -1px;
}
.page-services .content > h3 {
      margin: 45px 0 15px;
}
.page-services .content > table {
      border-collapse: collapse;
      width: 100%;
      background-color: #fff;
      color: #5e6d82;
      font-size: 14px;
      margin-bottom: 45px;
      line-height: 1.5em;
}
.page-services .content > table strong {
        font-weight: normal;
}
.page-services .content > table th {
        text-align: left;
        border-top: 1px solid #eaeefb;
        background-color: #EFF2F7;
        white-space: nowrap;
}
.page-services .content > table td, .page-services .content > table th {
        border-bottom: 1px solid #eaeefb;
        padding: 10px;
        max-width: 250px;
}
.page-services .content > table th:first-child, .page-services .content > table td:first-child {
        padding-left: 10px;
}
.page-services .page-component-up {
    background-color: #58b7ff;
    position: fixed;
    right: 20px;
    bottom: 10px;
    size: 50px;
    width: 50px;
    border-radius: 25px;
    cursor: pointer;
    opacity: 0.4;
    transition: .3s;
}
.page-services .page-component-up i {
      color: #fff;
      display: block;
      line-height: 50px;
      text-align: center;
      font-size: 22px;
}
.page-services .page-component-up.hover {
      opacity: 1;
}
.page-services .back-top-fade-enter,
  .page-services .back-top-fade-leave-active {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    opacity: 0;
}



