.g-fix-ios-overflow-scrolling-bug {
  -webkit-overflow-scrolling: auto !important;
}
.app {
  height: 100%;
  min-height: 100%;
}
.app .app-body {
    width: 100%;
}
@media screen and (max-width: 1059px) {
.app .app-body {
      padding-top: 80px;
}
}
@media screen and (max-width: 999px) {
.app .app-body {
      padding-top: 80px;
      padding-bottom: 15px;
}
.app.app-home-view .app-body {
      padding-top: 80px;
      padding-bottom: 0;
}
}
.hljs {
  line-height: 1.8;
  font-family: Menlo, Monaco, Consolas, Courier, monospace;
  font-size: 12px;
  padding: 18px 24px;
  background-color: #f9fafc;
  border: solid 1px #eaeefb;
  margin-bottom: 25px;
  border-radius: 4px;
  -webkit-font-smoothing: auto;
}
.app-body {
  margin-top: -80px;
  padding: 80px 0 120px;
  box-sizing: border-box;
  min-height: 100%;
}
.container,
.page-container {
  width: 1140px;
  padding: 0 30px;
  margin: 0 auto;
}
.page-container {
  padding-top: 55px;
}
.page-container h2 {
    font-size: 28px;
    color: #1f2d3d;
    margin: 0;
}
.page-container h3 {
    font-size: 22px;
}
.page-container h2, .page-container h3, .page-container h4, .page-container h5 {
    font-weight: normal;
    color: #1f2f3d;
}
.page-container h2:hover a, .page-container h3:hover a, .page-container h4:hover a, .page-container h5:hover a {
      opacity: .4;
}
.page-container h2 a, .page-container h3 a, .page-container h4 a, .page-container h5 a {
      float: left;
      margin-left: -20px;
      opacity: 0;
      cursor: pointer;
}
.page-container h2 a:hover, .page-container h3 a:hover, .page-container h4 a:hover, .page-container h5 a:hover {
        opacity: .4;
}
.page-container p {
    font-size: 14px;
    color: #5e6d82;
    line-height: 1.5em;
}
.page-container .tip {
    padding: 8px 16px;
    background-color: #ECF8FF;
    border-radius: 4px;
    border-left: #50bfff 5px solid;
}
.page-container .tip code {
      background-color: rgba(255, 255, 255, 0.7);
      color: #445368;
}
.demo {
  margin: 20px 0;
}
.upper {
  text-transform: uppercase;
}
.bg-blue-light {
  background-color: #58b7ff;
}
.color-box {
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  padding: 1em 14px 1em 14px;
  box-sizing: border-box;
  color: #fff;
  font-size: 14px;
  clear: both;
}
.color-box .icon {
    position: absolute;
    margin: 0;
    font-size: 18px;
}
.color-box .message-box-text {
    padding-left: 32px;
    font-weight: 500;
}
.color-box .message-box-text p {
      color: #fff;
}
@media (max-width: 1140px) {
.container,
  .page-container {
    width: 100%;
}
}
@media (max-width: 768px) {
.container,
  .page-container {
    padding: 0 20px;
}
}
.divide-30 {
  width: 100%;
  height: 30px;
}
.divide-20 {
  width: 100%;
  height: 20px;
}
[class*='form-'], [class*=' form-'] {
  margin-bottom: 15px;
}
.side-nav {
  width: 100%;
  box-sizing: border-box;
  padding-right: 30px;
}
.side-nav ul {
    padding: 0;
    margin: 0;
    overflow: hidden;
}
.side-nav ul li {
      list-style: none;
}
.side-nav .nav-item a {
    font-size: 16px;
    color: #5e6d82;
    line-height: 40px;
    height: 40px;
    margin: 0;
    padding: 0;
    text-decoration: none;
    display: block;
    position: relative;
    transition: all .3s;
}
.side-nav .nav-item a.is-active {
      color: #20a0ff;
}
.side-nav .nav-item a:hover {
      color: #20a0ff;
}
.side-nav .nav-item .nav-item a {
    display: block;
    height: 40px;
    line-height: 40px;
    font-size: 13px;
    padding-left: 24px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.side-nav .nav-item .nav-item a:hover {
      color: #20a0ff;
}
.side-nav .nav-group-title {
    font-size: 12px;
    color: #99a9bf;
    padding-left: 8px;
    line-height: 26px;
    margin-top: 10px;
}

